@import '../../constants/constatnts.scss';

.loader {
  border: 8px solid $loader_border;
  border-top: 8px solid $loader_border-top;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
